			setTimeout(() => {
				plus.screen.lockOrientation('landscape-primary');
			}, 500)
			// #endif
		},
		methods: {
			seeUser() {
				this.userIf = true;
			},
			close() {
				this.userIf = false;
			},
			closeTequan() {
				this.tequanIf = false;
			},
