				this.settingIf = false;
			},
			setSet(bool){
				this.audioIf = bool
			}
		},
		onUnload() {
			// #ifdef APP-PLUS
			plus.screen.lockOrientation('portrait-primary');
			// #endif
		},
	}
</script>

